import PropTypes from 'prop-types'
import React from 'react'
import { Link } from 'react-router-dom'
import { useToasts } from 'react-toast-notifications'
import { getDiscountPrice, getProductPrice } from '../../../helpers/product'

const MenuCart = ({ cartData, currency, deleteFromCart }) => {
  let cartTotalPrice = 0
  const { addToast } = useToasts()
  return (
    <div className='shopping-cart-content'>
      {cartData && cartData.length > 0 ? (
        <>
          <ul>
            {cartData.map((single, key) => {
              const discountedPrice = getDiscountPrice(
                single.price,
                single.discount
              )
              const finalProductPrice = (
                single.price * currency.currencyRate
              ).toFixed(2)
              const finalDiscountedPrice = (
                discountedPrice * currency.currencyRate
              ).toFixed(2)

              discountedPrice != null
                ? (cartTotalPrice += finalDiscountedPrice * single.quantity)
                : (cartTotalPrice += finalProductPrice * single.quantity)

              return (
                <li className='single-shopping-cart' key={key}>
                  <div className='shopping-cart-img'>
                    <Link to={process.env.PUBLIC_URL + '/product/' + single.id}>
                      <img
                        alt=''
                        src={process.env.PUBLIC_URL + single.image[0]}
                        className='img-fluid'
                      />
                    </Link>
                  </div>
                  <div className='shopping-cart-title'>
                    <h4>
                      <Link
                        to={process.env.PUBLIC_URL + '/product/' + single.id}
                      >
                        {' '}
                        {single.name}{' '}
                      </Link>
                    </h4>
                    <h6>Qty: {single.quantity}</h6>
                    <span>
                      {/* TODO: WP update {discountedPrice !== null
                        ? currency.currencySymbol + finalDiscountedPrice
                        : currency.currencySymbol + finalProductPrice} */}
                      {discountedPrice !== null
                        ? currency.currencySymbol + finalDiscountedPrice
                        : currency.currencySymbol +
                          getProductPrice(cartData[0]).toFixed(2)}
                    </span>
                    {single.selectedProductColor && single.selectedProductSize
                      ? ''
                      : // TODO: removed for WP product only
                    //  <div className="cart-item-variation">
                    //     <span>Color: {single.selectedProductColor}</span>
                    //     <span>Size: {single.selectedProductSize}</span>
                    //   </div>
                      ''}
                  </div>
                  <div className='shopping-cart-delete'>
                    <button onClick={() => deleteFromCart(single, addToast)}>
                      <i className='fa fa-times-circle' />
                    </button>
                  </div>
                </li>
              )
            })}
          </ul>
          <div className='shopping-cart-total'>
            <h4>
              Total :{' '}
              <span className='shop-total'>
                {/* TODO: change for WP change {currency.currencySymbol + cartTotalPrice.toFixed(2)} */}
                {currency.currencySymbol +
                  getProductPrice(cartData[0]).toFixed(2)}
              </span>
            </h4>
          </div>
          <div className='shopping-cart-btn btn-hover text-center'>
            <Link className='default-btn' to={process.env.PUBLIC_URL + '/cart'}>
              view cart
            </Link>
            <Link
              className='default-btn'
              to={process.env.PUBLIC_URL + '/main-checkout'}
            >
              checkout
            </Link>
          </div>
        </>
      ) : (
        <p className='text-center'>No items added to cart</p>
      )}
    </div>
  )
}

MenuCart.propTypes = {
  cartData: PropTypes.array,
  currency: PropTypes.object,
  deleteFromCart: PropTypes.func
}

export default MenuCart
