import PropTypes from 'prop-types'
import React, { useState, useEffect } from 'react'
import { animateScroll } from 'react-scroll'
import FooterCopyright from '../../components/footer/FooterCopyright'

const FooterOne = ({
  backgroundColorClass,
  spaceTopClass,
  spaceBottomClass,
  spaceLeftClass,
  spaceRightClass,
  containerClass,
  extraFooterClass,
  sideMenu
}) => {
  const [scroll, setScroll] = useState(0)
  const [top, setTop] = useState(0)

  useEffect(() => {
    setTop(100)
    window.addEventListener('scroll', handleScroll)
    return () => {
      window.removeEventListener('scroll', handleScroll)
    }
  }, [])

  const scrollToTop = () => {
    animateScroll.scrollToTop()
  }

  const handleScroll = () => {
    setScroll(window.scrollY)
  }

  return (
    <footer
      className={`footer-area ${
        backgroundColorClass || ''
      } ${spaceTopClass || ''} ${
        spaceBottomClass || ''
      } ${extraFooterClass || ''} ${
        spaceLeftClass || ''
      } ${spaceRightClass || ''}`}
    >
      <div className={`${containerClass || 'container'}`}>
        <div className='row'>
          <div
            className={`${
              sideMenu ? 'col-xl-2 col-sm-4' : 'col-lg-2 col-sm-4'
            }`}
          >
            {/* footer copyright */}
            <FooterCopyright
              footerLogo='/assets/img/logo/logo.png'
              spaceBottomClass='mb-30'
            />
          </div>
          <div
            className={`${
              sideMenu ? 'col-xl-2 col-sm-4' : 'col-lg-2 col-sm-4'
            }`}
          >
            <div className='footer-widget mb-30 ml-30'>
              <div className='footer-title'>
                <h3>ABOUT US</h3>
              </div>
              <div className='footer-list'>
                <ul>
                  <li>
                    <a
                      href='https://www.mykeeper.com/about-us/'
                      target='_blank'
                      rel='noopener noreferrer'
                    >
                      About Us
                    </a>
                  </li>
                  <li>
                    <a
                      href='https://www.mykeeper.com/'
                      target='_blank'
                      rel='noopener noreferrer'
                    >
                      For Families
                    </a>
                  </li>
                  <li>
                    <a
                      href='https://www.mykeeper.com/online-memorials-for-business/'
                      target='_blank'
                      rel='noopener noreferrer'
                    >
                      For Business
                    </a>
                  </li>
                  <li>
                    <a
                      href='https://www.mykeeper.com/features/'
                      target='_blank'
                      rel='noopener noreferrer'
                    >
                      Features
                    </a>
                  </li>
                </ul>
              </div>
            </div>
          </div>
          <div
            className={`${
              sideMenu ? 'col-xl-2 col-sm-4' : 'col-lg-2 col-sm-4'
            }`}
          >
            <div
              className={`${
                sideMenu
                  ? 'footer-widget mb-30 ml-95'
                  : 'footer-widget mb-30 ml-50'
              }`}
            >
              <div className='footer-title'>
                <h3>Products</h3>
              </div>
              <div className='footer-list'>
                <ul>
                  <li>
                    <a
                      href='https://www.mykeeper.com/keeper-plus-features/'
                      target='_blank'
                      rel='noopener noreferrer'
                    >
                      Keeper Plus
                    </a>
                  </li>
                  <li>
                    <a
                      href='https://www.mykeeper.com/contact/'
                      target='_blank'
                      rel='noopener noreferrer'
                    >
                      Contact
                    </a>
                  </li>
                  <li>
                    <a
                      href='https://www.talkdeath.com/'
                      target='_blank'
                      rel='noopener noreferrer'
                    >
                      Community
                    </a>
                  </li>
                  <li>
                    <a
                      href='https://www.mykeeper.com/access-api/'
                      target='_blank'
                      rel='noopener noreferrer'
                    >
                      Api Access
                    </a>
                  </li>
                </ul>
              </div>
            </div>
          </div>
          <div
            className={`${
              sideMenu ? 'col-xl-3 col-sm-4' : 'col-lg-2 col-sm-6'
            }`}
          >
            <div
              className={`${
                sideMenu
                  ? 'footer-widget mb-30 ml-145'
                  : 'footer-widget mb-30 ml-50'
              }`}
            >
              <div className='footer-title'>
                <h3>USEFUL LINKS</h3>
              </div>
              <div className='footer-list'>
                <ul>
                  <li>
                    <a
                      href='https://www.mykeeper.com/faq/'
                      target='_blank'
                      rel='noopener noreferrer'
                    >
                      Faq
                    </a>
                  </li>
                  <li>
                    <a
                      href='https://www.mykeeper.com/privacy/'
                      target='_blank'
                      rel='noopener noreferrer'
                    >
                      Privacy
                    </a>
                  </li>
                  <li>
                    <a
                      href='https://www.mykeeper.com/terms/'
                      target='_blank'
                      rel='noopener noreferrer'
                    >
                      Terms
                    </a>
                  </li>
                  <li>
                    <a
                      href='https://www.mykeeper.com/careers/'
                      target='_blank'
                      rel='noopener noreferrer'
                    >
                      Careers
                    </a>
                  </li>
                </ul>
              </div>
            </div>
          </div>
          <div
            className={`${
              sideMenu ? 'col-xl-3 col-sm-4' : 'col-lg-2 col-sm-6'
            }`}
          >
            <div
              className={`${
                sideMenu
                  ? 'footer-widget mb-30 ml-145'
                  : 'footer-widget mb-30 ml-50'
              }`}
            >
              <div className='footer-title'>
                <h3>FOLLOW US</h3>
              </div>
              <div className='footer-list'>
                <ul>
                  <li>
                    <a
                      href='https://www.facebook.com/KeeperMemorials/'
                      target='_blank'
                      rel='noopener noreferrer'
                    >
                      Facebook
                    </a>
                  </li>
                  <li>
                    <a
                      href='https://twitter.com/keepermemorials'
                      target='_blank'
                      rel='noopener noreferrer'
                    >
                      Twitter
                    </a>
                  </li>
                  <li>
                    <a
                      href='https://www.instagram.com/keepermemorials/'
                      target='_blank'
                      rel='noopener noreferrer'
                    >
                      Instagram
                    </a>
                  </li>
                  <li>
                    <a
                      href='//www.youtube.com'
                      target='_blank'
                      rel='noopener noreferrer'
                    >
                      Youtube
                    </a>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </div>
      <button
        className={`scroll-top ${scroll > top ? 'show' : ''}`}
        onClick={() => scrollToTop()}
      >
        <i className='fa fa-angle-double-up' />
      </button>
    </footer>
  )
}

FooterOne.propTypes = {
  backgroundColorClass: PropTypes.string,
  containerClass: PropTypes.string,
  extraFooterClass: PropTypes.string,
  sideMenu: PropTypes.bool,
  spaceBottomClass: PropTypes.string,
  spaceTopClass: PropTypes.string,
  spaceLeftClass: PropTypes.string,
  spaceRightClass: PropTypes.string
}

export default FooterOne
